// MyPage.js
import React, {useContext, useEffect, useState} from "react";
import {Link} from "react-router-dom";
import "./Mypage.css";
import axios from "axios";

const MyPage = () => {
    const [userId, setUserId] = useState(null);

    useEffect(() => {
        const fetchUserId = async () => {
            try {
                const response = await axios.get("/api/users/id");
                setUserId(response.data);
            } catch (error) {
                console.error("Error fetching user id:", error);
            }
        };

        fetchUserId();
    }, []);

    return (<div className="mypage">
            <div className="mypage-header">
                <h1>{userId ? `${userId}님의 마이페이지` : "Loading..."}</h1><br/>
            </div>
            <div className="mypage-main">
                <nav>
                    <ul className="circle-links">
                        <li>
                            <Link to="/mypage/profile">
                                <div className="circle-link">
                                    <div className="text">
                                        <h1>개인정보 수정</h1>
                                        <div className="icon">내 아이콘</div>
                                        <p>회원님의 개인정보를 열람합니다</p>
                                    </div>
                                </div>
                            </Link>
                        </li>
                        <li>
                            <Link to="/mypage/reservation">
                                <div className="circle-link">
                                    <div className="text">
                                        <h1>예매 내역</h1>
                                        <div className="icon">내 아이콘</div>
                                        <p>예매한 영화를 확인합니다</p>
                                    </div>
                                </div>
                            </Link>
                        </li>
                        <li>
                            <Link to="/mypage/purchase">
                                <div className="circle-link">
                                    <div className="text">
                                        <h1>상품 구매 내역</h1>
                                        <div className="icon">내 아이콘</div>
                                        <p>스토어에서 구매한<br/> 상품들을 확인합니다</p>
                                    </div>
                                </div>
                            </Link>
                        </li>
                    </ul>
                </nav>
            </div>
        </div>
    );
};

export default MyPage;
