// components/Movies.js

import React, {useEffect, useState} from 'react';
import "./Theater.css"
import axios from "axios";
import {Link} from "react-router-dom";

const Theater = () => {

    const [theaters, setTheaters] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                // API 호출
                const response = await axios.get('/api/theaters');
                setTheaters(response.data);
            } catch (error) {
                console.error('극장 정보를 불러오는 중 에러 발생:', error);
            }
        };
        fetchData();  // 비동기 함수를 호출하고 즉시 실행
    }, []);  // 빈 배열은 컴포넌트가 처음 마운트될 때만 실행되도록 함


    return <>
        <div className="theater-header">
            <img src="/images/theater.jpg"/>
        </div>
        <div className="theater-main">
            {theaters.map(theater => (
                <div className="theater-theater">
                    <div className="theater-content">
                        <div className="theater-name">
                            <div>{theater.theaterName}</div>
                            <Link to={{
                                pathname: `/reservation/${theater.theaterId}`,
                                state: {
                                    theater: theater
                                }
                            }}>
                                <button className="tc-btn">예매하러가기</button>
                            </Link>


                        </div>
                        <div>
                            <img src={`/images/${theater.theaterImageUrl}.jpg`} style={{width: '980px', height: '420px'}}/>
                        </div>
                    </div>
                    <div className="theater-box">
                        <div className="theater-info">
                            <div>{theater.location}</div>
                            <div>{theater.screeningHalls}관/ {theater.totalSeats}좌석</div>
                            <div>{theater.phoneNumber}</div>
                        </div>
                    </div>
                </div>))}
        </div>
    </>;
};

export default Theater;
