import React, { useEffect, useState } from "react";
import './ReservationHistory.css';
import axios from "axios";

const ReservationHistory = () => {
    const [reservations, setReservations] = useState([]);

    useEffect(() => {
        const fetchReservations = async () => {
            try {
                const response = await axios.get(`/api/reservation/history`);
                setReservations(response.data);
            } catch (error) {
                console.error("Error fetching reservations:", error);
            }
        };
        fetchReservations();
    }, []);

    // 날짜 및 시간 포맷팅 함수
    const formatDateTime = (dateTimeString) => {
        const dateTime = new Date(dateTimeString);
        const year = dateTime.getFullYear();
        const month = (dateTime.getMonth() + 1 < 10 ? '0' : '') + (dateTime.getMonth() + 1);
        const date = (dateTime.getDate() < 10 ? '0' : '') + dateTime.getDate();
        const hour = (dateTime.getHours() >= 12 ? '오후 ' : '오전 ') + (dateTime.getHours() % 12 || 12);
        const minute = (dateTime.getMinutes() < 10 ? '0' : '') + dateTime.getMinutes();
        return `${year}년 ${month}월 ${date}일, ${hour}시 ${minute}분`;
    };

    return (
        <div className="reservation-history-container">
            <h2>예매 내역</h2>
            <div className="reservation-list">
                {reservations.map((reservation, index) => (
                    <div className="reservation-item" key={index}>
                        <div className="movie-info">
                            <img src={`/images/`} alt="Movie Poster" className="movie-poster" />
                            <div className="movie-details">
                                <h3 className="movie-title">{reservation.movieTitle}</h3>
                                <p className="movie-date">상영 시작: {formatDateTime(reservation.startTime)}</p>
                                <p className="movie-time">상영 종료: {formatDateTime(reservation.endTime)}</p>
                            </div>
                        </div>
                        <div className="ticket-info">
                            <p className="ticket-type">예약 번호: {reservation.reservationId}</p>
                            <p className="ticket-type">예약자 ID: {reservation.userId}</p>
                            <p className="ticket-type">상영관 이름: {reservation.theaterName}</p>
                            <p className="ticket-type">위치: {reservation.location}</p>
                            <p className="ticket-price">좌석: {reservation.seats.join(', ')}</p>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default ReservationHistory;
