// UserProfile.js
import React, {useEffect, useState} from "react";
import "./UserProfile.css";
import axios from "axios";
import Swal from "sweetalert2";
const UserProfile = () => {
    const [user, setUser] = useState({});

    useEffect(() => {
        fetchUserData();
    }, []);

    const fetchUserData = async () => {
        try {
            const response = await axios.get("/api/users/info"); // 예시로 userId가 1인 사용자 정보 가져오기
            setUser(response.data);
        } catch (error) {
            console.error("Error fetching user data:", error);
        }
    };

    const updateUser = async () => {
        try {
            const response = await axios.put("/api/users/update", user);
            console.log("User updated:", response.data);
            Swal.fire({
                icon: "success",
                title: "수정 완료",
                text: "사용자 정보가 업데이트되었습니다.",
                confirmButtonText: "확인",
            }).then(() => {
                window.location.href = "/mypage"; // /mypage로 이동
            });
        } catch (error) {
            console.error("Error updating user:", error);
            Swal.fire({
                icon: "error",
                title: "수정 실패",
                text: "사용자 정보를 업데이트하는 중에 오류가 발생했습니다.",
                confirmButtonText: "확인",
            });
        }
    };

    const handleInputChange = (e) => {
        setUser({ ...user, [e.target.name]: e.target.value });
    };
    return (
        <div className="profile-container">
            <h2>개인정보 수정</h2>
            <div className="user-box">
                <label>아이디:</label>
                <input type="text" name="username" value={user.username} onChange={handleInputChange} />
                <label>비밀번호:</label>
                <input type="password" name="password" value={user.password} onChange={handleInputChange} />
                <button onClick={updateUser}>저장</button>
            </div>
        </div>
    );
};

export default UserProfile;
