import React from "react";
import './PurchaseHistory.css'
const PurchaseHistory = () => {
    // 임의의 구매 내역 데이터
    const orders = [
        {
            order_id: 1,
            product_id: 101,
            product_price: 20000,
            quantity: 2,
            order_date: "2024-01-28",
            is_ordered: true
        },
        {
            order_id: 2,
            product_id: 102,
            product_price: 15000,
            quantity: 1,
            order_date: "2024-01-25",
            is_ordered: false
        },
        // 추가적인 구매 내역 데이터
    ];

    return (
        <div className="purchase-history-container">
            <h2>상품 구매 내역</h2>
            <div className="order-list">
                {orders.map(order => (
                    <div key={order.order_id} className="order-item">
                        <div className="order-details">
                            <p>주문 번호: {order.order_id}</p>
                            <p>상품 ID: {order.product_id}</p>
                            <p>가격: {order.product_price}원</p>
                            <p>수량: {order.quantity}</p>
                            <p>주문 일자: {order.order_date}</p>
                        </div>
                        <div className="order-status">
                            {order.is_ordered ? (
                                <p>주문 완료</p>
                            ) : (
                                <p>주문 처리중</p>
                            )}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default PurchaseHistory;
