// app.js

import React from 'react';
import {BrowserRouter as Router, Route, Link, Routes} from 'react-router-dom';
import Home from './components/Home/Home';
import Movies from './components/Movies/Movies';
import Reservation from './components/Reservation/Reservation';
import Login from './components/User/Login/Login';
import Signup from './components/User/Signup/Signup';
import Nav from "./Nav";
import './App.css';
import Footer from "./Footer";
import Theater from "./components/Theater/Theater";
import Store from "./components/Store/Store";
import StoreDetail from "./components/Store/StoreDetail";
import Cart from "./components/User/Order/Cart";
import Purchase from "./components/User/Order/Purchase";
import PurchaseComplete from "./components/User/Order/PurchaseComplete";
import {UserProvider} from "./components/User/UserContext";
import MyPage from "./components/User/Mypage/Mypage";
import UserProfile from "./components/User/Mypage/UserProfile";
import ReservationHistory from "./components/User/Mypage/ReservationHistory";
import PurchaseHistory from "./components/User/Mypage/PurchaseHistory";

const App = () => {
    return (
        <React.StrictMode>
            <Router>
                <UserProvider>
                <Nav/>
                <Routes>
                    <Route path="/" element={<Home/>}/>
                    <Route path="/movies" element={<Movies/>}/>
                    <Route path="/store" element={<Store/>}/>
                    <Route path="/reservation" element={<Reservation/>}/>
                    <Route path="/reservation/:theaterId" element={<Reservation/>}/>
                    <Route path="/login" element={<Login/>}/>
                    <Route path="/signup" element={<Signup/>}/>
                    <Route path="/theater" element={<Theater/>}/>
                    <Route path="/store/detail/:productId" element={<StoreDetail/>}/>
                    <Route path="/cart" element={<Cart />} />
                    <Route path="/purchase" element={<Purchase />} />
                    <Route path="/purchase" element={<PurchaseComplete />} />
                    <Route path="/mypage" element={<MyPage />} />
                    <Route path="/mypage/profile" element={<UserProfile/>} />
                    <Route path="/mypage/reservation" element={<ReservationHistory/>} />
                    <Route path="/mypage/purchase" element={<PurchaseHistory/>} />
                </Routes>
                <Footer/>
                </UserProvider>
            </Router>
        </React.StrictMode>
    );
};

export default App;
