// Footer.js

import React from 'react';

const Footer = () => {
    return (
        <footer style={{ textAlign: 'center', padding: '40px', backgroundColor: '#333', color: '#fff' }}>
            <p style={{ fontSize: '1.2em', margin: 0 }}>
                © 2023 Your Website. All rights reserved.
            </p>
        </footer>
    );
};

export default Footer;
