import {Link, useNavigate, useParams} from "react-router-dom";
import React, {useEffect, useState} from "react";
import './StoreDetail.css'
import Cookies from "js-cookie";

const StoreDetail = () => {
    const {productId} = useParams();
    const [product, setProduct] = useState(null);
    const [quantity, setQuantity] = useState(1);
    const navigate = useNavigate();
    useEffect(() => {
        const fetchProductData = async () => {
            try {
                const response = await fetch(`/api/products/detail/${productId}`);
                if (response.ok) {
                    const data = await response.json();
                    setProduct(data);
                } else {
                    console.error('Failed to fetch product data:', response.status);
                }
            } catch (error) {
                console.error('Error fetching product data:', error);
            }
        };

        fetchProductData();
    }, [productId]);

    if (!product) {
        return <div>Loading...</div>;
    }


    const handleGoBack = () => {
        navigate(-1); // -1은 뒤로 가기를 의미합니다.
    };

    const handleIncreaseQuantity = () => {
        setQuantity(quantity + 1);
    };

    const handleDecreaseQuantity = () => {
        if (quantity > 1) {
            setQuantity(quantity - 1);
        }
    };

    const totalPrice = product.price * quantity;
    const handleButtonClick = async (action) => {
        try {
            const response = await fetch('/api/orders', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    productId,
                    quantity,
                }),
            });

            if (response.ok) {

                if (action === 'cart') {
                    navigate('/cart');
                } else if (action === 'purchase') {
                    navigate('/purchase');
                }

                setQuantity(1);

            } else {
                console.error('Failed to create/update order:', response.status);
            }
        } catch (error) {
            console.error('Error creating/updating order:', error);
        }
    }
    return (
        <div className="detail-main">
            <div className="detail-header">
                <div>{product.productName}</div>
                <img src="/images/arrow-left-solid.svg" alt="Back" onClick={handleGoBack}/>
            </div>
            <div className="detail-content">
                <div className="detail-img">
                    <img src={`/images/${product.imagePath}.jpg`} alt={product.name}/>
                </div>
                <div className="detail-text">
                    <div className="detail-price">{product.price.toLocaleString()}원</div>
                    <div className="detail-info-box">
                        <div className="detail-info-title">
                            <dt className="dt-info-title">상품구성</dt>
                            <dt className="dt-info-title">유효기간</dt>
                            <dt className="dt-info-title">상품교환</dt>
                        </div>
                        <div className="detail-info-content">
                            <dd>{product.productDetail}</dd>
                            <dd>구매일로부터 6개월 이내</dd>
                            <dd>사용가능 매장보기</dd>
                        </div>
                    </div>
                    <div className="quantityAndPrice">
                        <div className="quantity-controls">
                            <button className="minusBtn" onClick={handleDecreaseQuantity}>-</button>
                            <span>{quantity}</span>
                            <button className="plusBtn" onClick={handleIncreaseQuantity}>+</button>
                        </div>
                        <div className="qap-price">{totalPrice.toLocaleString()}원</div>
                    </div>
                    <div className="totalPrice">
                        총 구매금액
                        <span>{totalPrice.toLocaleString()}원</span>
                    </div>
                    <div className="btn-box">
                        <button className="cartBtn" onClick={() => handleButtonClick('cart')}>
                            장바구니
                        </button>
                        <button className="buyBtn" onClick={() => handleButtonClick('purchase')}>
                            구매하기
                        </button>
                    </div>
                </div>
            </div>
            <dl>
                <dt className="ddTitle">이용안내</dt>
                <dd className="ddText">
                    <p>• 극장 사정에 따라 일부 메뉴 제공이 어려울 수 있습니다.<br/>
                        • 해당 기프트콘은 오프라인 매점에서 실제 상품으로 교환할 수 있는 온라인 상품권입니다.<br/>
                        • <strong>구매 후 전송받으신 기프트콘은,</strong><br/>
                        사용가능한 매점에서 지정된 해당 상품으로만 교환이 가능합니다.<br/>
                        (사용가능은 '상품교환'에서 확인 가능하며, 추가 상품을 포함하여 구매 시 지점에 따라 사용이 불가 할 수 있으니 발송되는 기프트콘의 정보를 확인해주시기
                        바랍니다.)<br/>
                        해당 상품 내에서 팝콘 맛 혹은 사이즈 변경 시 추가 비용 발생합니다.<br/>
                        교환 완료한 상품의 환불 및 반품은 불가합니다.<br/>
                        • <strong>유효기간 연장을 신청하는 경우,</strong><br/>
                        유효기간은 발급일로부터 5년 이내 횟수 제한 없이 기간 연장 가능하며, 1회 연장 시 3개월(92일) 단위로 유효기간이 연장됩니다.&nbsp;&nbsp;<br/>
                        단, 이벤트 경품 및 프로모션 상품의 경우 유효기간 연장이 불가할 수 있습니다.<br/>
                        유효기간 만료 이후에는 결제금액의 90% 환불이 가능합니다.&nbsp;&nbsp;<br/>
                        • 매점상품 기프트콘은 극장 매점에서 상품 교환시 CJ ONE 적립이 가능합니다.<br/>
                        • 상기 이미지는 실제와 다를 수 있습니다.</p>
                </dd>
                <dt className="ddTitle">취소/환불</dt>
                <dd className="ddText"><p>• 구매자는 최초 유효기간 이내에 결제금액의 100%에 대해 결제취소/환불이 가능하며, 최초 유효기간 만료 후에는 수신자가 결제금액의
                    90%에 대해 환불 요청 가능합니다.<br/>
                    • 단, 이미 사용된 기프트콘에 대해서는 결제취소/환불 신청이 불가합니다.&nbsp;<br/>
                    • 결제취소/환불 방법<br/>
                    결제취소는 모바일App,웹 &gt; MY &gt; 결제내역조회 &gt; 스토어 or 홈페이지 &gt; My page &gt; 스토어 &gt; 결제내역의 해당 주문 상세내역에서
                    가능합니다.<br/>
                    (기프트콘은 구매일로부터 60일 이내 결제취소 가능하며, 카드 결제취소 가능 기간이 경과하였을 경우, 고객센터로 연락주시면 됩니다)<br/>
                    환불은 모바일App,웹 &gt; MY &gt; 기프트콘 or 홈페이지 &gt; My page &gt; 스토어 &gt; 내 기프트콘에서 환불을 원하는 기프트콘 등록 후 진행
                    가능하며, 비회원의 경우 고객센터로 신청 가능합니다.<br/>
                    단 이 때, 본인 확인 및 계좌 확인 절차가 진행됩니다.<br/>
                    • 수신자는 선물받은 기프트콘의 수신거절을 요청할 수 있으며, 이 경우 구매자에게 취소 및 환불에 대한 안내가 이루어집니다.&nbsp;<br/>
                    • 결제취소 가능 기간이 경과한 후 수신자가 수신거절을 요청할 경우 구매자에게 기프트콘이 재발송됩니다.<br/>
                    • 고객센터 1544-1122</p></dd>
            </dl>
        </div>
    );
};
export default StoreDetail;