// signup.js

import React, { useState } from 'react';
import {Link, useNavigate} from 'react-router-dom';
import axios from 'axios';
import Swal from 'sweetalert2'; // sweetalert2를 import
import './Signup.css';

const SignupForm = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const navigate = useNavigate();
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const idRegex = /^[a-zA-Z0-9]{8,}$/;
            const passwordRegex = /^(?=.*[!@#$%^&*(),.?":{}|<>]).{8,}$/;

            if (!idRegex.test(username)) {
                Swal.fire({
                    icon: 'error',
                    title: 'ID 오류',
                    text: 'ID는 8글자 이상이어야 합니다.',
                });
                return;
            }

            if (!passwordRegex.test(password)) {
                Swal.fire({
                    icon: 'error',
                    title: '비밀번호 오류',
                    text: '비밀번호는 8글자 이상이며, 특수문자를 포함해야 합니다.',
                });
                return;
            }

            if (password !== confirmPassword) {
                Swal.fire({
                    icon: 'error',
                    title: '비밀번호 일치 오류',
                    text: '비밀번호가 일치하지 않습니다.',
                });
                return;
            }

            const response = await axios.post('/api/users/signup', {
                username,
                password,
            });
            console.log(response.data);
            // 회원가입 성공 후 추가 동작 (예: 로그인 페이지로 리디렉션)
            navigate('/login');
            Swal.fire({
                icon: 'success',
                title: '회원가입 성공',
                text: '회원가입이 성공적으로 완료되었습니다.',
            });
        } catch (error) {
            console.error('회원가입 실패:', error.message);
            // 회원가입 실패 시 에러 처리 (예: 에러 메시지 표시)
        }
    };

    return (
        <div className="login-container">
            <div className="login-title">회원가입</div>
            <form className="login-form" onSubmit={handleSubmit}>
                <input
                    className="login-input"
                    type="text"
                    placeholder="user name"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                />
                <br />
                <input
                    className="login-input"
                    type="password"
                    placeholder="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                <br />
                <input
                    className="login-input"
                    type="password"
                    placeholder="confirm password"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                />
                <br />
                <button className="loginbtn" onClick={handleSubmit}>
                    회원가입
                </button>
            </form>
            <Link to="/login" className="linkToLogin">
                이미 회원이신가요? 로그인하러가기
            </Link>
        </div>
    );
};

export default SignupForm;
