import React, {useEffect} from "react";
import './Cart.css'
import {useState} from "react";
import {useNavigate} from "react-router-dom";
import axios from "axios";
import Swal from 'sweetalert2';
const Cart = () => {
    const [allItemsChecked, setAllItemsChecked] = useState(true);
    const [selectedItemCount, setSelectedItemCount] = useState(0);
    const navigate = useNavigate();
    const [totalPrice, setTotalPrice] = useState(0);
    const [cartItems, setCartItems] = useState([]);

    useEffect(() => {
        // API 호출 및 데이터 받아오기
        fetch('/api/cart')
            .then(response => response.json())
            .then((data) => setCartItems(data.map(item => ({...item, checked: true}))));
    }, []);
    useEffect(() => {
        // 선택된 아이템 수 계산
        const count = cartItems.filter((item) => item.checked).length;
        setSelectedItemCount(count);

        // 모든 아이템이 선택되었는지 확인
        setAllItemsChecked(count === cartItems.length);
        const totalPrice = cartItems
            .filter((item) => item.checked)
            .reduce((acc, item) => acc + item.quantity * item.productPrice, 0);

        setTotalPrice(totalPrice);
    }, [cartItems]);
    const handleAllItemsCheckboxClick = () => {
        // 모든 아이템의 체크 상태를 토글
        const updatedCartItems = cartItems.map((item) => ({
            ...item,
            checked: !allItemsChecked,
        }));
        setCartItems(updatedCartItems);
    };

    const handleItemCheckboxClick = (productId) => {
        // 특정 아이템의 체크 상태를 토글
        const updatedCartItems = cartItems.map((item) =>
            item.productId === productId ? {...item, checked: !item.checked} : item
        );
        setCartItems(updatedCartItems);
    };

    const handleDeleteSelectedItems = async () => {
        try {
            // 선택된 상품 ID 목록 가져오기
            const selectedProductIds = cartItems
                .filter((item) => item.checked)
                .map((item) => item.productId);

            const result = await Swal.fire({
                title: '정말 삭제하실건가요?',
                text: '"네"버튼 클릭 시 장바구니에서 삭제됩니다.',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: '네',
                cancelButtonText:'아니요'
            });

            // If the user clicks "Yes" in the confirmation dialog
            if (result.isConfirmed) {
                // 서버에 삭제 요청 보내기
                await axios.post("/api/orders/delete", selectedProductIds);

                // 클라이언트에서 선택된 상품들 제거
                const updatedCartItems = cartItems.filter((item) => !item.checked);
                setCartItems(updatedCartItems);

                // Show success message
                Swal.fire('삭제완료!', '', 'success');
            }
        } catch (error) {
            console.error("상품 삭제 실패:", error.message);
            console.log("에러 응답:", error.response); // 에러 응답 확인
            // Show error message
            Swal.fire('Error', 'Failed to delete items.', 'error');
        }
    };

    const handlePurchase = () => {
        // 선택된 상품들의 정보 가져오기
        const selectedProducts = cartItems.filter(item => item.checked);

        // Purchase 페이지로 이동하면서 선택된 상품 정보 전달
        navigate('/purchase', { state: { selectedProducts } });
    };
    return (
        <div className="cart-main">
            <div className="cart-header">
                <div className="clicked cart-header-content">
                    <div className="cart-icon">
                        <img src='/images/cart.png'/>
                    </div>
                    <div className="cart-header-letter">
                        <div className="step">
                            STEP 01
                        </div>
                        <div className="letter">
                            장바구니
                        </div>
                    </div>
                </div>
                <img className="right-arrow" src='/images/right.svg'/>
                <div className="cart-header-content">
                    <div className="cart-icon">
                        <img src='/images/pay.svg'/>
                    </div>
                    <div className="cart-header-letter">
                        <div className="step">
                            STEP 02
                        </div>
                        <div className="letter">
                            결제하기
                        </div>
                    </div>
                </div>
                <img className="right-arrow" src='/images/right.svg'/>
                <div className="cart-header-content">
                    <div className="cart-icon">
                        <img className="chc" src='/images/complete.svg'/>
                    </div>
                    <div className="cart-header-letter">
                        <div className="step">
                            STEP 03
                        </div>
                        <div className="letter">
                            결제완료
                        </div>
                    </div>
                </div>
            </div>

            <div className="cart-info">
                <div className="cart-info-1">
                    <div>
                        <input
                            type="checkbox"
                            checked={allItemsChecked}
                            onChange={handleAllItemsCheckboxClick}
                        />
                    </div>

                    <div>상품명</div>
                    <div>판매금액</div>
                    <div>수량</div>
                    <div>구매금액</div>
                </div>
                {cartItems.map(item => (
                    <div className="cart-info-2" key={item.productId}>
                        <div>
                            <input
                                type="checkbox"
                                checked={item.checked}
                                onChange={() => handleItemCheckboxClick(item.productId)}
                            />
                        </div>
                        <div className="ci1">
                            <div className="ci-img"><img src={`images/${item.imagePath}.jpg`} alt={item.productName}/>
                            </div>
                            <div>
                                <div className="product-name">
                                    {item.productName}
                                </div>
                                <div className="product-detail">
                                    {item.productDetail}
                                </div>
                            </div>
                        </div>
                        <div className="ci2">{item.productPrice.toLocaleString()}원</div>
                        <div className="ci3">{item.quantity}</div>
                        <div className="ci4">{(item.quantity * item.productPrice).toLocaleString()}원</div>
                    </div>
                ))}
            </div>

            <div className="cart-warning">
                <div className="delete-cart" onClick={handleDeleteSelectedItems}>
                    선택상품 삭제({selectedItemCount})
                </div>
                <div>장바구니에 담긴 상품은 최대 30일까지 보관됩니다.</div>
            </div>
            <div className="final-pay">
                <div className="fp-header">
                    <div>
                        총 상품 금액
                    </div>
                    <div>
                        할인 금액
                    </div>
                    <div>
                        총 결제 예정 금액
                    </div>
                </div>
                <div className="fp-main">
                    <div className="origin">{totalPrice.toLocaleString()}원</div>
                    <div className="discount">0원</div>
                    <div className="final">{totalPrice.toLocaleString()}원</div>
                </div>

                <button className="buyBtn payBtn" onClick={handlePurchase}>
                    구매하기
                </button>
            </div>
        </div>
    );
};

export default Cart;