// components/Store.js

import React, {useEffect, useState} from 'react';
import './Store.css';
import Package from './Package';
import Ticket from './Ticket';
import GiftCard from './GiftCard';
import Combo from './Combo';
import Popcorn from './Popcorn';
import Beverage from './Beverage';
import Snack from './Snack';
import axios from "axios";
import {Link} from "react-router-dom";

const Store = () => {
    const [activeSection, setActiveSection] = useState('package'); // 초기 섹션은 패키지
    const [products, setProducts] = useState([]);
    const [cartCount, setCartCount] = useState(0);

    useEffect(() => {
        const fetchCartCount = async () => {
            try {
                const response = await axios.get('/api/orders/count');
                setCartCount(response.data);
            } catch (error) {
                console.error('Error fetching cart count:', error.message);
            }
        };

        fetchCartCount();
    }, []);
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await axios.get(`/api/products/${activeSection}`);
                setProducts(response.data);
            } catch (error) {
                console.error('Error fetching product data:', error);
            }
        };

        fetchData(); // 컴포넌트가 마운트될 때 초기 데이터 가져오기
    }, [activeSection]); // activeSection이 변경될 때마다 실행

    const handleNavClick = (section) => {
        setActiveSection(section);
    };

    const getNavClassName = (section) => {
        return activeSection === section ? 'active' : 'inactive';
    };

    return (
        <>
            <div className="store-main">
                <div className="store-title">
                    <div>스토어</div>
                </div>
                <div className="store-nav">
                    <div className="store-nav-content">
                        <div onClick={() => handleNavClick('package')} className={getNavClassName('package')}>
                            패키지
                        </div>
                        <div onClick={() => handleNavClick('ticket')} className={getNavClassName('ticket')}>
                            영화관람권
                        </div>
                        <div onClick={() => handleNavClick('giftCard')} className={getNavClassName('giftCard')}>
                            기프트카드
                        </div>
                        <div onClick={() => handleNavClick('combo')} className={getNavClassName('combo')}>
                            콤보
                        </div>
                        <div onClick={() => handleNavClick('popcorn')} className={getNavClassName('popcorn')}>
                            팝콘
                        </div>
                        <div onClick={() => handleNavClick('beverage')} className={getNavClassName('beverage')}>
                            음료
                        </div>
                        <div onClick={() => handleNavClick('snack')} className={getNavClassName('snack')}>
                            스낵
                        </div>
                    </div>
                    <Link to='/cart'>
                    <div className="cart">
                        장바구니
                        <span className="cartcnt">{cartCount}</span>
                    </div>
                    </Link>
                </div>

                {activeSection === 'package' && <Package products={products} />}
                {activeSection === 'ticket' && <Ticket products={products} />}
                {activeSection === 'giftCard' && <GiftCard products={products} />}
                {activeSection === 'combo' && <Combo products={products} />}
                {activeSection === 'popcorn' && <Popcorn products={products} />}
                {activeSection === 'beverage' && <Beverage products={products} />}
                {activeSection === 'snack' && <Snack products={products} />}
            </div>
        </>
    );
};

export default Store;
