import React, {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
const Package = ({products}) => {
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const images = products.map(product => new Promise(resolve => {
            const img = new Image();
            img.onload = () => resolve();
            img.src = `/images/${product.imagePath}.jpg`;
        }));

        Promise.all(images)
            .then(() => setLoading(false))
            .catch(error => console.error('이미지 로딩 중 오류 발생:', error));
    }, [products]);

    return (
        <div className="card-main">
            <div className="card-title">
                <h1>패키지</h1>
                <div>마음을 담아 선물하세요</div>
            </div>
            {loading ? (
                <div className="spinner"></div>
            ) : (
                <div className="card-content">
                    {products.map(product => (
                        <Link key={product.productId} to={`../store/detail/${product.productId}`} className="card">
                            <div className="card-img">
                                <img src={`/images/${product.imagePath}.jpg`} alt={product.name} />
                            </div>
                            <div className="card-details">
                                <div className="card-name">{product.productName}</div>
                                <div className="card-detail">{product.productDetail}</div>
                                <div className="card-price">{product.price}원</div>
                            </div>
                        </Link>
                    ))}
                </div>
            )}
        </div>
    );
};
export default Package;
