// components/Swiper/MovieChartCarousel.js

import React, {useEffect, useState} from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css';
import './MovieChartCarousel.css'; // 스타일 파일을 import
const MovieChartCarousel = () => {
    const [movieChartData, setMovieChartData] = useState([]);
    useEffect(() => {
        // Spring Boot 백엔드에서 영화 가져오기
        fetch("/api/movies")
            .then(response => response.json())
            .then(data => setMovieChartData(data.map(movie => ({
                ...movie,
                releaseDate: new Date(movie.releaseDate).toLocaleDateString('ko-KR')
            }))))
            .catch(error => console.error('영화를 가져오는 중 에러 발생:', error));
    }, []);
    return (
        <div className="movie-chart-swiper-container">
            <Swiper
                slidesPerView={5}
                spaceBetween={10}
                navigation={{
                    nextEl: '.movie-chart-swiper-button-next',
                    prevEl: '.movie-chart-swiper-button-prev',
                }}
                loop={false}
            >
                {movieChartData.map((movie) => (
                    <SwiperSlide key={movie.id} style={{ marginRight: '70px' }}>
                        <div className="movie-chart-item">
                            <img
                                src={`/images/${movie.imageUrl}`}
                                alt={movie.title}
                                style={{ width: '170px', height: '234px' }}
                            />
                            <p className="movie-title">{movie.title}</p>
                        </div>
                    </SwiperSlide>
                ))}
            </Swiper>

            {/* Navigation arrows */}
            <div className="movie-chart-swiper-button-prev"></div>
            <div className="movie-chart-swiper-button-next"></div>
        </div>
    );
};

export default MovieChartCarousel;
