import React, {useEffect, useState} from 'react';
import "./Movies.css"
import {SwiperSlide} from "swiper/react";

const Movies = () => {
    const [movies, setMovies] = useState([]);

    useEffect(() => {
        // Spring Boot 백엔드에서 영화 가져오기
        fetch("/api/movies")
            .then(response => response.json())
            .then(data => setMovies(data.map(movie => ({
                ...movie,
                releaseDate: new Date(movie.releaseDate).toLocaleDateString('ko-KR')
            }))))
            .catch(error => console.error('영화를 가져오는 중 에러 발생:', error));
    }, []);

    return (
        <>
            <div className="Movies-title">
                <div>무비차트</div>
            </div>
            <div className="movie-list">
                {movies.map((movie) => (
                    <div key={movie.movieId} className="movie-card">
                        <div className="movie-chart-item">
                            <div className="movie-chart-header">
                                No.{movie.movieId}
                            </div>
                            <img
                                src={`images/${movie.imageUrl}`}
                                alt={movie.title}
                                style={{width: '170px', height: '234px'}}
                            />
                            <p className="movie-title">{movie.title}</p>
                            <div className="movie-info">
                                <div><strong>개봉일</strong>:&nbsp;&nbsp;&nbsp;{movie.releaseDate}</div>
                                <div><strong>장르</strong>:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{movie.genre}</div>
                                <div><strong>상영시간</strong>:{movie.durationMinutes}분</div>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </>
    );
};

export default Movies;
