import React, {useEffect} from "react";
import './Cart.css'
import './Purchase.css'
import {useLocation} from "react-router-dom";
import axios from "axios";
import Swal from 'sweetalert2';
const Purchase = () => {
    const location = useLocation();
    const selectedProducts = location.state.selectedProducts || [];
    // 총 상품 금액 계산
    const totalAmount = selectedProducts.reduce((acc, item) => {
        return acc + item.quantity * item.productPrice;
    }, 0);
    const handlePurchase = async () => {
        try {
            // 선택된 상품들의 product_id 배열 추출
            const selectedProductIds = selectedProducts.map((item) => item.productId);
            console.log(selectedProductIds)
            // 서버로 선택된 상품들의 product_id 배열 전송
            await axios.post("/api/orders/purchase",  selectedProductIds);

            Swal.fire({
                icon: 'success',
                title: '결제가 완료되었습니다!',
                showConfirmButton: false,
                timer: 1500, // 1.5초 후에 자동으로 사라짐
            }).then(() => {
                // 일정 시간이 지난 후에 홈페이지로 이동
                setTimeout(() => {
                    window.location.href = '/';
                }, 1500);
            });
        } catch (error) {
            console.error("구매 실패:", error.message);
        }
    };
    return (
        <div className="cart-main">
            <div className="cart-header">
                <div className="cart-header-content">
                    <div className="cart-icon">
                        <img src='/images/cart.png'/>
                    </div>
                    <div className="cart-header-letter">
                        <div className="step">
                            STEP 01
                        </div>
                        <div className="letter">
                            장바구니
                        </div>
                    </div>
                </div>
                <img className="right-arrow" src='/images/right.svg'/>
                <div className="clicked cart-header-content">
                    <div className="cart-icon">
                        <img src='/images/pay.svg'/>
                    </div>
                    <div className="cart-header-letter">
                        <div className="step">
                            STEP 02
                        </div>
                        <div className="letter">
                            결제하기
                        </div>
                    </div>
                </div>
                <img className="right-arrow" src='/images/right.svg'/>
                <div className="cart-header-content">
                    <div className="cart-icon">
                        <img className="chc" src='/images/complete.svg'/>
                    </div>
                    <div className="cart-header-letter">
                        <div className="step">
                            STEP 03
                        </div>
                        <div className="letter">
                            결제완료
                        </div>
                    </div>
                </div>
            </div>

            <div className="cart-info">
                <div className="cart-info-1 asd">
                    <div>상품명</div>
                    <div>판매금액</div>
                    <div>수량</div>
                    <div>구매금액</div>
                </div>
                {selectedProducts.map((item) => (
                    <div className="cart-info-2" key={item.productId}>
                        <div className="ci1 purchase-ci1">
                            <div className="ci-img"><img src={`images/${item.imagePath}.jpg`} alt={item.productName} /></div>
                            <div>
                                <div className="product-name">{item.productName}</div>
                                <div className="product-detail">{item.productDetail}</div>
                            </div>
                        </div>
                        <div className="ci2 purchase-ci2">{item.productPrice.toLocaleString()}원</div>
                        <div className="ci3">{item.quantity}</div>
                        <div className="ci4">{(item.quantity * item.productPrice).toLocaleString()}원</div>
                    </div>
                ))}
            </div>
            <div className="final-pay">
                <div className="fp-header">
                    <div>
                        총 상품 금액
                    </div>
                    <div>
                        할인 금액
                    </div>
                    <div>
                        총 결제 예정 금액
                    </div>
                </div>
                <div className="fp-main">
                    <div className="origin">{totalAmount.toLocaleString()}원</div>
                    <div className="discount">0원</div>
                    <div className="final">{totalAmount.toLocaleString()}원</div>
                </div>

                <button className="buyBtn payBtn" onClick={handlePurchase}>
                    결제하기
                </button>
            </div>
        </div>
    );
};

export default Purchase;