// Nav.js
import React, {useEffect, useState} from 'react';
import {Link, useLocation, useNavigate} from 'react-router-dom';
import axios from 'axios';
import Swal from 'sweetalert2';
import { useUserContext } from './components/User/UserContext';
const Nav = () => {
    const { isLoggedIn, setIsLoggedIn } = useUserContext();
    const location = useLocation();
    const navigate = useNavigate();
    useEffect(() => {
        // 페이지 로드 시 로그인 상태 확인
        checkLoginStatus();
    }, []);

    const checkLoginStatus = async () => {
        try {
            // 서버의 check-login 엔드포인트를 호출하여 로그인 상태 확인
            const response = await axios.get('/api/users/check-login');
            setIsLoggedIn(response.data);
        } catch (error) {
            console.error('로그인 상태 확인 실패:', error.message);
        }
    };

    const handleLogout = async () => {
        try {
            // 로그아웃 요청
            await axios.post('/api/users/logout');
            await Swal.fire('로그아웃 되었습니다!');

            // 상태 업데이트
            setIsLoggedIn(false);
            navigate('/');
        } catch (error) {
            console.error('로그아웃 실패:', error.message);
        }
    };

    return (
        <nav className="navbar">
            <div className="navbar-content">
                <div className="nc1">
                    <div className="header-content">
                        <div className="logo">
                            <img className="logo" src="/images/logo.png" alt="로고" />
                        </div>
                        <div className="header-icons">
                            {isLoggedIn ? (
                                <a className="header-user" onClick={handleLogout}>
                                    <div>
                                        <img className="header-img" src="/images/user-logout.svg" alt="로그아웃 아이콘" />
                                    </div>
                                    로그아웃
                                </a>
                            ) : (
                                <>
                                    <Link to="/login" className="header-user">
                                        <div>
                                            <img className="header-img" src="/images/user-lock.svg" alt="로그인 아이콘" />
                                        </div>
                                        로그인
                                    </Link>
                                    <Link to="/signup" className="header-user">
                                        <div>
                                            <img className="header-img" src="/images/user-add.svg" alt="회원가입 아이콘" />
                                        </div>
                                        회원가입
                                    </Link>
                                </>
                            )}
                            <Link to="/mypage" className="header-user">
                                <div>
                                    <img className="header-img" src="/images/user-mypage.svg" alt="마이페이지 아이콘" />
                                </div>
                                마이페이지
                            </Link>
                            <Link to="/qna" className="header-user">
                                <div>
                                    <img className="header-img" src="/images/user-qna.svg" alt="문의사항 아이콘" />
                                </div>
                                문의사항
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="nc2">
                    <div className="nav">
                        <Link to="/" className={`nav-item ${location.pathname === '/' ? 'active' : ''}`}>
                            홈
                        </Link>
                        <Link to="/movies" className={`nav-item ${location.pathname === '/movies' ? 'active' : ''}`}>
                            영화
                        </Link>
                        <Link to="/theater" className={`nav-item ${location.pathname === '/theater' ? 'active' : ''}`}>
                            극장
                        </Link>
                        <Link to="/reservation" className={`nav-item ${location.pathname.startsWith('/reservation') ? 'active' : ''}`}>
                            예매
                        </Link>
                        <Link to="/store" className={`nav-item ${location.pathname === '/store' ? 'active' : ''}`}>
                            스토어
                        </Link>
                    </div>
                </div>
            </div>
        </nav>
    );
};

export default Nav;
